<style>
  .home .swipe{
    height: 56vw;
  }
  .home .swipe img{
    width: 100%;
  }
  .home .mint-swipe-indicator{
    background: #fff;
    opacity: .5;
  }
  .home .mint-swipe-indicator.is-active {
    background: #26a2ff;
    opacity: 1;
  }
  .home .commodity,
  .home .nav{
    display: flex;
    width: 100%;
    padding: .7rem;
    box-sizing: border-box;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .home>.commodity{
    padding-bottom: 60px;
  }
  .home .nav a {
    width: 33.333%;
  }
  .home .nav a>img {
    width: 100%;
  }
  .home .commodity .card{
    width: 48%;
    height: 235px;
    border: 1px solid rgb(214, 213, 213);
    box-shadow: 0 0 3px 3px #eee;
    margin-bottom: 8px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  .home .card>img{
    height: 55%;
    width: 100%;
  }
  .home .card>.title{
    margin: 0 2px;
    font-size: 13px;
  }
  .home .card>.price{
    text-align: center;
    margin: 5px 0;
  }
  .home .card>.price>span{
    color: red;
  }
  .home .card>.price>del{
    font-size: 12px;
    color: rgb(184, 184, 184);
  }
  .home .card>.buy{
    width: 50%;
    margin: 0 auto;
  }
</style>
<template>
  <div class="home" @touchstart="a" @touchend="b">
    <div class="swipe">
      <mt-swipe :auto="swipeTime">
        <mt-swipe-item><img src="../assets/images/1.jpg" alt=""></mt-swipe-item>
        <mt-swipe-item><img src="../assets/images/1.jpg" alt=""></mt-swipe-item>
        <mt-swipe-item><img src="../assets/images/1.jpg" alt=""></mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="nav">
      <a v-for="(item,i) in imgList" :key="i" href="javascript:;"><img :src="require('../assets/images/nav'+item+'.png')" alt=""></a>
    </div>
    <div class="commodity">
      <div class="card" v-for="(item,i) in comList" :key="i" @click="goShopping">
        <img src="../assets/images/1.jpg">
        <p class="title">adidas阿迪达斯 男式 场下休闲篮球鞋S83700</p>
        <div class="price">
          <span>￥560.00</span><del>￥1888.00</del>
        </div>
        <div class="buy">
          <router-link to="/details"><mt-button type="primary" size="small">立即购买</mt-button></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data(){
    return {
      swipeTime: 50000,
      comList:18,
      imgList:6,
      startY:0
    }
  },
  methods:{
    a(e){
      this.startY = e.changedTouches[0].pageY
    },
    b(e){
      if(e.changedTouches[0].pageY > this.startY && !this.$store.state.bottom){
        this.$store.commit("setBottomT")
      }else if(e.changedTouches[0].pageY>135 && e.changedTouches[0].pageY < this.startY){
        this.$store.commit("setBottomF")
      }
    },
    goShopping(e){
      if(e.target.nodeName == "IMG"||e.target.nodeName == "P"){
        this.$router.push({path:'/details'})
      }
    }
  }
}
</script>
