<style>
  .header{
    position: relative;
  }
  .header .search{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 100vw;
    z-index: 3;
    border: 1px solid red;
    box-sizing: border-box;
    transition: .3s ease-out;
  }
</style>
<template>
  <div class="header">
    <mt-header :title="title">
      <mt-button @click="goUp" v-if="back" slot="left" icon="back">返回</mt-button>
      <mt-button @click="search" icon="search" slot="right"></mt-button>
    </mt-header>
    <div :style="show?'left:0px;':''" class="search">

    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      title: this.$route.name,
      showHeader: true,
      show: false
    }
  },
  methods:{
    search(){
      console.log(111)
      this.show = true
    },
    getRoute(){
      this.title = this.$route.name
    },
    goUp(){
      this.$router.go(-1)
      console.log("返回")
    }
  },
  computed:{
    back(){
      const routes = ['/details','/address','/add-address','/order','/allorder']
      if(routes.indexOf(this.$route.path) != -1){
        return true
      }else{
        return false
      }
    }
  },
  watch:{
    '$route':'getRoute'
  }
}
</script>