<template>
<div class="tabbar" :style="bottom?'bottom:0px;':'bottom:-55px;'">
  <mt-tabbar v-model="selected">
    <mt-tab-item id="index">
      <span slot="icon" class="iconfont icon-gzhuanhuan"></span>
      首页
    </mt-tab-item>
    <mt-tab-item id="class">
      <span slot="icon" class="iconfont icon-icon"></span>
      分类
    </mt-tab-item>
    <mt-tab-item id="car">
      <span slot="icon" class="iconfont icon-qicheqianlian-1-copy"></span>
      购物车
    </mt-tab-item>
    <mt-tab-item id="vip">
      <span slot="icon" class="iconfont icon-huiyuanzhongxin"></span>
      会员中心
    </mt-tab-item>
  </mt-tabbar>
</div>
</template>

<script>
import '@/assets/font/iconfont.css'
export default {
  data(){
    return {
      selected: "index"
    }
  },
  methods:{
    getPath(){
      this.selected = this.$route.path.slice(1)
    }
  },
  computed:{
    bottom(){
      return this.$store.state.bottom
    }
  },
  beforeMount(){

  },
  mounted(){

},

  watch:{
    '$route':'getPath',
    selected(val,oldval){
      const rout = this.$route.path.slice(1)
      switch(val){
        case 'index':
          if(rout!=val)
          this.$router.replace('/index');
        break;
        case 'class':
          if(rout!=val)
          this.$router.replace('/class');
        break;
        case 'car': 
          if(rout!=val)
          this.$router.replace('/car');
        break;
        case 'vip':
          if(rout!=val)
          this.$router.replace('/vip');
        break;
      }
    }
  }
}
</script>

<style>
  .iconfont{
    font-size: 24px;
  }
  .tabbar{
    right: 0;
    left: 0;
    position: fixed;
    z-index: 2;
    transition: .3s;
    -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
  }
</style>