import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    bottom:true,
    arr:[{status: false, price: 456.66, size: 41, count: 1}]
  },
  getters: {
    
  },
  mutations: {
    setBottomF(state){
      state.bottom = false
    },
    setBottomT(state){
      state.bottom = true
    },
    addArr(state,commodity){
      state.arr.push(commodity)
    },
    spliceArr(state,arr){
      for(var i=0;i<arr.length;i++){
        state.arr.splice(arr[i],1)
      }
    }
    // removeArr(state,index)
  },
  actions: {
  },
  modules: {
  }
})
